import BigNumber from 'bignumber.js'
import NftAbi from "../nftAbi.json";
import React from 'react';
import './App.css';
import styled from 'styled-components';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {NftContract} from "../constants/index";
import Modal from "./Modal/Modal";

class NFT extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          loading: false,
          networkId: 0,
          balanceOf:0,
          pass:"NFT",
          nbNFT: new BigNumber(0),
          nbNFTprice: new BigNumber(9000).div(10**5),
          totalMINT:0,
          mintPrice: new BigNumber(9000).times(10**13),
          maxRuneApes: 500,
          totalSupply: 0,
          tokenRewardsClaimable:0,
          publicActive: false,
          privateActive:false,
          transaction:'',
        }
      }


  networkId = 1;
  bigNumber =   "1000000000000000000";
  NftContract = NftContract;
  NftApp = null;
  maxTX = 7;

  async checkpublicActiveTimestamp(){
    let PrivatActive = await this.NftApp.methods.activeGenesis().call()
    let PublicActive = await this.NftApp.methods.activeGenesisPublic().call()
    this.setState({publicActive: PublicActive})
    this.setState({privateActive: PrivatActive})
  }


  async componentWillMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
      if(this.props.account !== this.state.account && this.props.web3 !== null)
        await this.loadContract()
    }

 async loadContract() {
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    await this.connect()
    await this.checkpublicActiveTimestamp()
  }

  async connect() {

    //console.log(this.props.account)
    if(this.props.account){
        this.setState({ account: this.props.account })
        this.setState({
          totalMINT:  Number(await this.NftApp.methods.balanceOf(this.props.account).call()),
          PublicActive:  (await this.NftApp.methods.activeGenesisPublic().call()),
          totalSupply:  Number((await this.NftApp.methods.totalSupply().call())),
          nbNFT: new BigNumber(1),
          earlyClaim: Number(await this.NftApp.methods.genesisClaim(this.props.account).call())
        })
    }
  }


 async updateNFT(){
        let value = document.getElementById("walletToCheck")  as HTMLInputElement ;
        if (new BigNumber(value.value).eq(new BigNumber(1))){
            this.setState({pass:"NFT"})
            this.setState({nbNFT:new BigNumber(1)})
            this.setState({nbNFTprice:new BigNumber(9000).div(10**5)})
        }
        else if (new BigNumber(value.value).gt(new BigNumber(this.maxTX))){
            this.setState({pass:"NFTs"})
            this.setState({nbNFT:new BigNumber(this.maxTX)})
            this.setState({nbNFTprice: new BigNumber(9000).div(10**5).times(new BigNumber(this.maxTX))})
        }
        else{
            this.setState({pass:"NFTs"})
            this.setState({nbNFT:new BigNumber(this.maxTX)})
            this.setState({nbNFTprice: new BigNumber(9000).div(10**5).times(new BigNumber(this.maxTX))})
        }
     }


  resolve(transactionHash){
   this.setState({loading:true})
   //console.log(transactionHash,"transactionHash")
   this.setState({transaction:"https://etherscan.io/tx/"+transactionHash})
  }

  reject(err){
   this.setState({loading:false}); 
   //console.log(err)
  }

  loaderModal(receipt){
    this.setState({loading:false}); 
    //console.log(receipt['transactionHash'])
    this.connect()
  }
    

  closeModal(){
    this.setState({loading:false}); 
  }

 async claimToken(){

    let bigvalue = this.state.nbNFT
    let amountETH = new BigNumber(bigvalue).times(this.state.mintPrice)


    console.log(bigvalue,amountETH)

    let proof = []
    await new Promise((resolve, reject) => {
       this.NftApp.methods.publicGenesis(bigvalue.toNumber())
      .send({from: this.state.account, value: amountETH})
        .once('transactionHash', (transactionHash) => {
            this.resolve(transactionHash)
        })
        .on('confirmation', (confirmationNumber) => {
            //this.resolve(confirmationNumber)
        })
        .on('error', err => this.reject(err))
        .then( receipt =>
            this.loaderModal(receipt)
        );
    })
  }

    render() {
     if(!this.props.web3){
        return(
            <AppWrapper>
            <ConnectWalletWrapper>
               <Wrapper>
                 <Title>
                 "Unlock Your Metamask or click Connect"
                  </Title>
               </Wrapper>
            </ConnectWalletWrapper>
            </AppWrapper>
        )
      }
      else
    {
    return (
      <AppWrapper>c
                <Modal title="Pending transaction" onClose={() => this.closeModal()} show={this.state.loading}>
                        <ButtonEther><StyledLinkH href={this.state.transaction}  rel="noreferrer" target="_blank">EtherScan transaction</StyledLinkH></ButtonEther>
                </Modal>
        {this.state.publicActive && this.state.totalSupply < this.state.maxRuneApes &&
            <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    {this.state.totalSupply}/{this.state.maxRuneApes} have been minted.
                </Title>
                <Spacer/>
                  <Title>
                    Buy your NFT purchase up to {this.maxTX} per transaction.
                  </Title>
                  <Spacer/>
              </Wrapper>
              { this.state.earlyClaim < this.maxTX &&
              <>
              <MintInput onChange={() => this.updateNFT()} id="walletToCheck" placeholder={this.state.nbNFT} />
              <Spacer/>
              <ButtonClaim onClick={this.state.account === '' ? '' : () => this.claimToken()}>{this.state.account === '' ? 'Not connected' : `Claim ${this.state.nbNFT} ${this.state.pass} ${this.state.nbNFTprice}ETH`}</ButtonClaim>
              <Spacer/>
              </>
              }
            </ConnectWalletWrapper>
        }


        {!this.state.publicActive &&  

           <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    Genesis Public Access is closed.
                </Title>
                </Wrapper>
            </ConnectWalletWrapper>

        }

        {this.state.publicActive &&  this.state.totalSupply >= this.state.maxRuneApes &&

           <ConnectWalletWrapper>
              <Wrapper>
                <Title>
                    Genesis Public Access is over.
                </Title>
                </Wrapper>
            </ConnectWalletWrapper>

        }

      </AppWrapper>
    );
    }
  }
}

